import React from 'react';
const sensors = props => {
  // const sensorElements = props.sensors.map(s => <li key={s.id}>{s.id}: {s.observed}</li>);
  const temperatureElement = <li>Temperature: {props.temperature}</li>;

  return (<div><ul>
    {temperatureElement}
  </ul></div>);
};
sensors.propTypes = { temperature: React.PropTypes.number };
export default sensors;



