/*
{
 visibilityFilter: 'SHOW_ALL',
 sensors: [
 {
 id:'TEMPERATURE',
 observed: '-10',
 visible: true,
 },
 {
 id:'HUMIDITY',
 observed: '98',
 visible: true
 }
 ]
 }
 */

const temperatureReducer = (state = 0, action) => {
  switch (action.type) {
    case 'GET_TEMPERATURE':
      // console.log('Payload ${action.payload}');
      if (typeof action !== 'undefined') {
        // return action.payload.retMst.jsonMessage.state.desired.value;
        console.log(`Payload ${action.payload.data.payload}`);

        return JSON.parse(action.payload.data.payload).state.payload.value;
      } else {
        console.log(`Returning state ${state}`)
        return state;
      }
    default: return state;
  }
};

export default temperatureReducer;



